import { Component } from '@angular/core';
import { Observable, of } from "rxjs";
import {Equipment} from './equipment';
import {EquipmentSet} from './equipmentSet';
import {EquipmentType} from './equipmentType';
import {EQUIPMENT_TYPES} from './equipmentTypeList';
import {SET_LIST} from './equipmentSetList';
import {HERO_LIST} from './heroList';
import {JOB_SIGN_BONUS_LIST} from './jobSignBonusList';
import {Hero} from './hero';
import {UserSet} from './userSet';
import {StarStatLevel} from './starStatLevel';
import {Stats} from './stats';


declare var $: any;

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css']
})
export class AppComponent {
	jobZodiacBonusList = JOB_SIGN_BONUS_LIST;
	equipTypes = EQUIPMENT_TYPES;
	addEquipType : EquipmentType = EQUIPMENT_TYPES[0];
	addEquip = new Equipment();
	rarities = ['Epic','Heroic','Rare','Good','Normal'];
	sets = SET_LIST;
	heroes = HERO_LIST;
	selectedHero:Hero=new Hero();

	weapons: Array<Equipment> = [];
	armors: Array<Equipment> = [];
	helmets: Array<Equipment> = [];
	necklaces: Array<Equipment> = [];
	rings: Array<Equipment> = [];
	boots: Array<Equipment> = [];
	artifact = {attack:0,health:0};
	
	bestStatName='';
	bestStatMin='';
	statFilters = [];
	statBonuses:Array<EquipmentSet> = [];
	
	equipmentSetStats = [];
	equipmentSetFinal = [];
	
	equipmentSets:UserSet[] = [];
	setName:string='';

	file:any;
	
	selectWeaponIdx:number=0;
	selectHelmetIdx:number=0;
	selectArmorIdx:number=0;
	selectNecklaceIdx:number=0;
	selectRingIdx:number=0;
	selectBootIdx:number=0;


	finalStats = new Stats();
	equipStats = new Stats();
	selectedStar = new StarStatLevel();
	selectedAwakening :number = 0;
	awakenStat = new Stats();
	statList = Object.getOwnPropertyNames(this.finalStats);
	bestStat:string = '';
	bestStatMaxEquip = 5;
	
	addFilter():void {
		this.statFilters.push({statName:this.bestStatName,statMin:this.bestStatMin,resultCnt:0});
	}
	
	generateBest():void {
		let bestStat = this.bestStat;
		
		let bHelms = this.helmets;
		let bWeps = this.weapons;
		let bArmors = this.armors;
		let bNecks = this.necklaces;
		let bRings = this.rings;
		let bBoots = this.boots;
		
		let combos = []; // {stats:{}, idx:[]}
		
		this.statFilters.forEach(function(s){
			s.resultCnt=0;
		});
		
		bHelms  = this.findBest(bHelms,this.statFilters[0].statName,this.bestStatMaxEquip);
		bWeps   = this.findBest(bWeps,this.statFilters[0].statName,this.bestStatMaxEquip);
		bArmors = this.findBest(bArmors,this.statFilters[0].statName,this.bestStatMaxEquip);
		bNecks  = this.findBest(bNecks,this.statFilters[0].statName,this.bestStatMaxEquip);
		bRings  = this.findBest(bRings,this.statFilters[0].statName,this.bestStatMaxEquip);
		bBoots  = this.findBest(bBoots,this.statFilters[0].statName,this.bestStatMaxEquip);
		combos = this.generateAllCombos(bWeps,bHelms,bArmors,bNecks,bRings,bBoots,this.statFilters[0]);
		this.statFilters[0].resultCnt=combos.length;
		
		let matchesAll = [];
		
		combos.forEach(function(c){
			let passes = true;

			for (let i=1;i<this.statFilters.length;i++) {
				const filter = this.statFilters[i];
				if (!this.passesFilter(c.stats,filter)) {
					passes = false;
					break;
				} else {
					this.statFilters[i].resultCnt = this.statFilters[i].resultCnt+1;
				}
			}
			
			if (passes) {
				matchesAll.push(c);
			}

		},this);
		
		combos = matchesAll;
		//best = filterBestSets(combos);
		//let best = this.getBestX(combos,bestStat,this.bestStatMaxEquip);
		//console.log("best", combos);
		let cnt=0;
		
		
		combos.slice(0,5).forEach(function(bestSet) {
			let us:UserSet = new UserSet();
			cnt++;
			us.name=bestStat+cnt;
			us.equipment = [bWeps[bestSet.idx[0]],bHelms[bestSet.idx[1]],bArmors[bestSet.idx[2]],bNecks[bestSet.idx[3]],bRings[bestSet.idx[4]],bBoots[bestSet.idx[5]]];
			
			if(!this.equipmentSets) {
				this.equipmentSets = [];
			}
			this.equipmentSets.push(us);
		},this);
		

		
		//let finalBest:Stats = this.calculateFinalStats(best[0].stats);
		//this.equipmentSetStats.push({name:"speed",stats:finalBest});
		//this.equipmentSetFinal.push({name:"speed",stats:best[0].stats});
	}
	
	getArrayFromIdx(array,idx):Array<any> {
		const unique = idx.filter((v, i, a) => a.indexOf(v) === i);
		let newArr = [];
		
		unique.forEach(function(i) {
			newArr.push(array[i]);
		});
		
		return newArr;
	}
	
	generateAllCombos(bWeps,bHelms,bArmors,bNecks,bRings,bBoots,filter):Array<any> {
		let combos = [];
		for(let i=0;i<bWeps.length;i++) {
			for(let j=0;j<bHelms.length;j++) {
				for(let k=0;k<bArmors.length;k++) {
					for(let l=0;l<bNecks.length;l++) {
						for(let m=0;m<bRings.length;m++) {
							for(let n=0;n<bBoots.length;n++) {
								const finalStats = this.addAllStats([bWeps[i],bHelms[j],bArmors[k],bNecks[l],bRings[m],bBoots[n]]);
								
								if (this.passesFilter(finalStats,filter)) {
									combos.push({stats:finalStats,idx:[i,j,k,l,m,n]});
								}
							}
						}
					}
				}
			}
		}
		
		return combos;
	}
	
	filter0(equipments):Array<Equipment>{
		
		let finalEquips:Array<Equipment> = [];
		
		equipments.forEach(function(e){
			let passed = false;
			this.statFilters.forEach(function(filter){
				if (e.stats[filter.statName] > 0) {
					passed = true;
				}
			});
			
			if (passed) {
				finalEquips.push(e);
			}
		}, this);

		//console.log("filter0",equipments.length, finalEquips.length);
		if (finalEquips.length ===0) {
			return equipments.slice(0,1);
		}
		return finalEquips;
	}
	
	passesFilter(item,filter):boolean {
		let passed = true;
		if (item[filter.statName] < filter.statMin) {
			passed = false;
		}
		
		//console.log("checked " + filter.statName, item[filter.statName], filter.statMin,passed);
		return passed;
	}
	
	filterBestSets(allcombos, filters):Array<any>{
		let finalList = [];
		
		allcombos.forEach(function(item){
			let passed = true;
			filters.forEach(function(filter){
				if (item[filter.statName] < filter.statMin) {
					passed = false;
				}
			});
			if (passed) {
				finalList.push(item);
			}
		},this);
		return finalList;
	}
	
	findBest(arr,statName,topNums):Array<any> {
		//console.log("checking stat [" + statName + "] ", arr);
		const sortedArr = arr.sort((a,b) => (this.getNumber(a.stats[statName]) < this.getNumber(b.stats[statName])?1:-1));
		//console.log("sorted",sortedArr);
		
		let topX = sortedArr.slice(0,topNums);
		let setName = '';
		this.sets.forEach(function(set) {
			//console.log("set",set.bonusStat,statName);
			if (set.bonusStat === statName) {
				setName = set.name;
			}
		});
		
		//console.log("setName",setName);
		for (let i=topNums;i<sortedArr.length;i++) {
			if (sortedArr[i].set === setName) {
				topX.push(sortedArr[i]);
			}
		}
		
		//console.log("best find",topX);
		return topX;
	}
	
	getBestX(arr,statName,topNums):Array<any> {
		const sortedArr = arr.sort((a,b,) => (a.stats[statName] < b.stats[statName]?1:-1));
		
		return sortedArr.slice(0,topNums);
	}
	
	addSet():void {
		let newSet:Array<Equipment> = [];
		newSet.push(this.weapons[this.selectWeaponIdx]);
		newSet.push(this.helmets[this.selectHelmetIdx]);
		newSet.push(this.armors[this.selectArmorIdx]);
		newSet.push(this.necklaces[this.selectNecklaceIdx]);
		newSet.push(this.rings[this.selectRingIdx]);
		newSet.push(this.boots[this.selectBootIdx]);
		
		let us:UserSet = new UserSet();
		us.equipment = newSet;
		if (this.setName) {
			us.name=this.setName;
		}
		
		if(!this.equipmentSets) {
			this.equipmentSets = [];
		}
		this.equipmentSets.push(us);
		//console.log("added set", us);
		
		this.setName='';
	}

	addEquipment():void {
		//console.log("Adding", this.addEquip);

		switch(this.addEquip.type) {
			case ("Weapon"): this.weapons.push(this.addEquip); break;
			case ("Helmet"): this.helmets.push(this.addEquip); break;
			case ("Armor"): this.armors.push(this.addEquip); break;
			case ("Necklace"): this.necklaces.push(this.addEquip); break;
			case ("Ring"): this.rings.push(this.addEquip); break;
			case ("Boot"): this.boots.push(this.addEquip); break;
		}

		//console.log("weapons", this.weapons);
		//console.log("armors", this.armors);

		this.addEquip = new Equipment();
		this.CalculateCurrentStats();
	}
	addEquipmentInit():void {
		this.addEquip = new Equipment();
	}
	
	showSet(i):void {
		const userSet = this.equipmentSets[i];
		
		let equipStats = this.addAllStats(userSet.equipment);
		let finalStats = this.calculateFinalStats(equipStats);
		
		this.equipmentSetStats.push({name:userSet.name,stats:equipStats});
		this.equipmentSetFinal.push({name:userSet.name,stats:finalStats});
	}
	removeSet(i):void {
		this.remove(this.equipmentSetStats,i);
		this.remove(this.equipmentSetFinal,i);
	}
	
	remove(arr,i):void {
		//console.log("remove", arr,i);
		arr.splice(i,1);
		
		this.CalculateCurrentStats();
	}
	
	edit(arr,i):void {
		//console.log("edit",arr,i);
		this.addEquip = arr[i];
		this.addEquipType = this.equipTypes.filter(item => {
			return item.name === arr[i].type;
		})[0];
		$('#exampleModal').modal('show');
	}
	
	save():void {
		this.addEquip = new Equipment();
		this.CalculateCurrentStats();
	}

	selectWeapon(w):void {
		this.selectWeaponIdx = w;
		this.CalculateCurrentStats();
	}

	selectArmor(w):void {
		this.selectArmorIdx = w;
		this.CalculateCurrentStats();
	}

	selectNecklace(w):void {
		this.selectNecklaceIdx = w;
		this.CalculateCurrentStats();
	}

	selectRing(w):void {
		this.selectRingIdx = w;
		this.CalculateCurrentStats();
	}

	selectHelmet(w):void {
		this.selectHelmetIdx = w;
		this.CalculateCurrentStats();
	}

	selectBoot(w):void {
		this.selectBootIdx = w;
		this.CalculateCurrentStats();
	}
	
	selectHero():void {
		this.selectedStar = this.selectedHero.statsbyStar[0];
		this.selectAwakening(this.selectedAwakening);
		this.CalculateCurrentStats();
		
		this.updateSetStats();
	}
	
	updateSetStats():void {
		for(let i=0;i<this.equipmentSetFinal.length;i++) {
			this.equipmentSetFinal[i].stats = this.calculateFinalStats(this.equipmentSetStats[i].stats);
		}
	}
	
	selectStar(e):void {
		if (!e) {
			return;
		}
		this.selectedStar = e;
		this.CalculateCurrentStats();
		this.updateSetStats();
	}
	
	selectAwakening(n):void {
		//console.log("selected " + n);
		this.selectedAwakening = n;
		this.awakenStat = new Stats();
		
		let jobZodiacBonus = this.jobZodiacBonusList.filter(item => {
			return item.job === this.selectedHero.job;
		})[0].bonusStats;
		
		let bonusStatsList = jobZodiacBonus.filter(item => {
			return item.sign === this.selectedHero.zodiacSign;
		})[0].bonusStats;
		
		for(let i=0;i<n;i++) {
			this.addStats(this.awakenStat,bonusStatsList[i]);
		}

		this.CalculateCurrentStats();
		this.updateSetStats();
		//console.log("awaken stats", this.awakenStat);
	}
	

	addStats(dest,src):void{
		//console.log("addstats", dest, src);
		for (var property in src) {
			if (src.hasOwnProperty) {
				dest[property] = dest[property] + this.getNumber(src[property]);
			}
		}
	}
	
	CalculateCurrentStats():void {
		let equips:Equipment[] = [];
		
		equips.push(this.weapons[this.selectWeaponIdx]);
		equips.push(this.helmets[this.selectHelmetIdx]);
		equips.push(this.armors[this.selectArmorIdx]);
		equips.push(this.necklaces[this.selectNecklaceIdx]);
		equips.push(this.rings[this.selectRingIdx]);
		equips.push(this.boots[this.selectBootIdx]);
	
		this.equipStats = this.addAllStats(equips);
		this.finalStats = this.calculateFinalStats(this.equipStats);
	}
	
	addAllStats(equips):Stats {
		if (!equips[0]) {
			return new Stats();
		}
		this.statBonuses = [];
		let finalStats:Stats = new Stats();
		
		equips.forEach(function(equip) {
			if (equip) {
				this.addStats(finalStats, equip.stats);
			}
		}, this);
		
	
		// calculate bonus
		this.sets.forEach(function(currentSet) {
			const setCnt = this.getSetCnt(equips, currentSet.name);
			
			const completeSetCnt = setCnt/currentSet.requiredItems>>0;
			//console.log("complete set " + currentSet.name + " " + completeSetCnt);
			for(let i=0;i<completeSetCnt;i++) {
				this.statBonuses.push(currentSet);
			}
		},this);

		for (let i=0;i<this.statBonuses.length;i++) {
			finalStats[this.statBonuses[i].bonusStat] = finalStats[this.statBonuses[i].bonusStat] + this.statBonuses[i].bonusStatValue;

		}
		
		//console.log('final', finalStats);
		return finalStats;
	}
	
	calculateFinalStats(stats):Stats {
		let finalStats:Stats = new Stats();
		const baseHealth = Math.floor(this.selectedStar.stats.health * (1 + this.awakenStat.healthPct/100) + this.awakenStat.health); // round down
		finalStats.health = (baseHealth * (1 + stats.healthPct/100)) +  // equipment health pct
							stats.health + // equipment health
							this.getNumber(this.artifact.health); // artifact health
							
		const baseAttack = Math.floor(this.selectedStar.stats.attack * (1 + this.awakenStat.attackPct/100) + this.awakenStat.attack); // round down
		finalStats.attack = (baseAttack * (1 + stats.attackPct/100)) +  // equipment attack pct
							stats.attack + // equipment attack
							this.getNumber(this.artifact.attack); // artifact attack
							
		const baseDef = Math.floor(this.selectedStar.stats.defense * (1 + this.awakenStat.defensePct/100) + this.awakenStat.defense); // round down
		finalStats.defense = Math.floor(baseDef * (1 + stats.defensePct/100)) +  // equipment defense pct. roll down
							stats.defense; // equipment defense
							
		finalStats.speed = this.selectedStar.stats.speed + this.awakenStat.speed + (this.selectedStar.stats.speed * stats.speedPct)/100 + stats.speed;
		finalStats.critRatePct = this.selectedStar.stats.critRatePct + stats.critRatePct + this.awakenStat.critRatePct;
		finalStats.critDamagePct = this.selectedStar.stats.critDamagePct + stats.critDamagePct + this.awakenStat.critDamagePct
		finalStats.dualPct = this.selectedStar.stats.dualPct + stats.dualPct;
		finalStats.effectivenessPct = this.selectedStar.stats.effectivenessPct + stats.effectivenessPct + this.awakenStat.effectivenessPct;
		finalStats.resistancePct = this.selectedStar.stats.resistancePct + stats.resistancePct + this.awakenStat.resistancePct;
		
		return finalStats;
	}
	
	getSetCnt(equips, setName):number {
		let selectedSets = [];
		
		equips.forEach(function(equip) {
			selectedSets.push(equip.set);
		});

		return selectedSets.filter(item => {
			return item === setName;
		}).length;
	}
	
	getNumber(n):number {
		if (!n) return 0;
		if(n == NaN) return Number(n);
		return Number(n);
	}
	
	// ************************ File things
		
	private setting = {
		element: {
			dynamicDownload: null as HTMLElement
		}
	}

	fileChanged(e) {
		this.file = e.target.files[0];
	}

	fakeEquips() {
		return of({weapons:this.weapons,armors:this.armors,helmets:this.helmets,necklaces:this.necklaces,rings:this.rings,boots:this.boots,equipmentSets:this.equipmentSets});
	}

	downloadEquips() {
		this.fakeEquips().subscribe((res)=>{
			this.dyanmicDownloadByHtmlTag({
				fileName:'equips',
				text: JSON.stringify(res)
			})
		})
	}

	uploadDocument() {
		let fileReader = new FileReader();
		fileReader.onload = (e) => {
			let equipsIn;
			if (typeof fileReader.result === 'string') {
				equipsIn = JSON.parse(fileReader.result);

				this.weapons = equipsIn.weapons;
				this.helmets = equipsIn.helmets;
				this.armors = equipsIn.armors;
				this.necklaces = equipsIn.necklaces;
				this.rings = equipsIn.rings;
				this.boots = equipsIn.boots;
				this.equipmentSets = equipsIn.equipmentSets;

				this.CalculateCurrentStats();
				console.log("loaded Data",equipsIn);
			}
		}
		fileReader.readAsText(this.file);
	}

	private dyanmicDownloadByHtmlTag(arg: {fileName: string, text: string}) {
		if (!this.setting.element.dynamicDownload) {
			this.setting.element.dynamicDownload = document.createElement('a');
		}
		const element = this.setting.element.dynamicDownload;
		const fileType = arg.fileName.indexOf('.json') > -1 ? 'text/json' : 'text/plain';
		element.setAttribute('href', `data:${fileType};charset=utf-8,${encodeURIComponent(arg.text)}`);
		element.setAttribute('download', arg.fileName);

		var event = new MouseEvent("click");
		element.dispatchEvent(event);
	}
	
	// *****************************************
}
